.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;

  &.hidden {
    height: 125px;
    .cart-items {
      height: 20px;
      overflow: hidden;
    }
  }

  .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;

    span {
      margin: 0 auto;
    }
  }

  button {
    margin-top: auto;
  }
}
